.customer-reviews {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  ////----------стилизация под ретину-----------------
  // Mobile screen
  background-image: url(../images/customer-reviews/background/backgroud_mobile@1x.png);
  // retina mobile screen
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/customer-reviews/background/backgroud_mobile@2x.png);
  }

  // Tablet screen
  @media screen and (min-width: 768px) {
    background-image: url(../images/customer-reviews/background/background_tablet@1x.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/customer-reviews/background/background_tablet@2x.png);
    }
  }

  // Desktop screen
  @media screen and (min-width: 1200px) {
    background-image: url(../images/customer-reviews/background/backgroud_desktop@1x.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/customer-reviews/background/backgroud_desktop@2x.png);
    }
  }
}
.customer-reviews .container {
  @media screen and (max-width: 767px) {
    min-width: 320px;
    max-width: 480px;
    padding: 0 20px 393px;
  }
  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding: 0 32px 379px;
  }
  @media screen and (min-width: 1200px) {
    max-width: 1200px;
    padding: 0 93px 228px;
  }
}
.gallery_img {
  display: block;
  margin: 0 auto;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    transform: translateY(-32px);
    margin-bottom: 48px;
    border-radius: 8px;
  }

  @media screen and (min-width: 768px) {
    transform: translateY(-144px);
    margin-bottom: -44px;
    border-radius: 16px;
  }

  @media screen and (min-width: 1200px) {
    transform: translateY(-98px);
    margin-bottom: 42px;
    border-radius: 24px;
  }
}

.customer_img {
  display: block;
  margin: 0 auto;
  margin-bottom: 32px;

  border-radius: 43px;
}
.customer_review {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 35px 39px;

  @media screen and (min-width: 768px) {
    padding: 28px 80px;

    font-weight: 500;
    font-size: 14px;
    line-height: 1.86;
  }
  @media screen and (min-width: 1200px) {
    padding: 43px 109px;
    margin-left: 23px;
    margin-right: 23px;
    font-size: 16px;
  }

  background-color: $background-color;
  border-radius: 10px;
  border: none;
  margin-bottom: 32px;

  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 2.58;

  text-align: center;
}
.customer_name {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.86;
  margin-bottom: 12px;
  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
    font-size: 16px;
  }

  text-align: center;
  color: $secondary-color;
}

.customer__pagination {
  &.swiper-pagination {
    bottom: 0;
  }
  .swiper-pagination-bullet {
    background: $pagination-color;
    &.swiper-pagination-bullet-active {
      background: $background-color;
    }
  }
}
